<script lang="ts">
	import { createEventDispatcher, getContext } from "svelte";
	import CustomButton from "../components/customButton.svelte";
	import type { Characteristic } from "../models/characteristic";
	import { EventType, TAEvent } from "../models/event";
	import { BeatmapDifficulty } from "../models/match";
	import type { PreviewBeatmapLevel } from "../models/previewBeatmapLevel";
	import type { SongFinished } from "../models/songFinished";
	import { CompletionType } from "../models/songFinished";
	import CustomInput from "../components/customInput.svelte";
	import Select from "../components/Select.svelte";
	import type { Beatmap } from "../models/beatmap";
	import type { QualifierEvent } from "../models/qualifierEvent";
	import type { GameplayParameters } from "../models/gameplayParameters";
	import { GameOptions, GameplayModifiers } from "../models/gameplayModifiers";
	import CustomCheckbox from "../components/customCheckbox.svelte";

	// export let results: SongFinished[] = [];

	// results.sort((a, b) => {
	// 	return b.Score - a.Score;
	// });

	const { close } = getContext("simple-modal");
	let bsURL = "";
	export let onOkay = (data?) => {};
	export let event: QualifierEvent;
	let selectedLevel: PreviewBeatmapLevel = null;
	let selectedDiff: BeatmapDifficulty = null;
	let SelectedCharacteristic: Characteristic = null;

	let mapOptions = [];
	for (const modifier in GameOptions) {
		if (isNaN(Number(modifier))) {
			if (modifier == "None") continue;
			mapOptions.push({
				name: modifier.replace(/([A-Z])/g, " $1").trim(),
				value: GameOptions[modifier],
				isSelected: false,
			});
		}
	}

	async function loadSong() {
		if (bsURL == "" || !bsURL) return null;
		loading = true;
		let songId = bsURL;
		if (bsURL.includes("beatsaver")) {
			// return null;
			songId = bsURL.split("beatmap/")[1];
			if (songId.includes("&")) songId = songId.split("&")[0];
		}
		let req = await fetch(
			`https://beatsaver.com/api/maps/detail/${songId}`,
			{
				method: "GET",
				headers: {
					"User-Agent":
						"TAClient/1.0.0 (+https://github.com/Dannypoke03)",
				},
			}
		);
		let songData = await req.json();
		let hash: string = songData.hash;
		let characteristics: Characteristic[] = [];
		// curMatch.SelectedDifficulty = 0;
		// console.log(songData);
		for (const characteristic of songData.metadata.characteristics) {
			let diffs: BeatmapDifficulty[] = [];
			for (const diffLabel in characteristic.difficulties) {
				if (
					Object.prototype.hasOwnProperty.call(
						characteristic.difficulties,
						diffLabel
					)
				) {
					if (characteristic.difficulties[diffLabel] != null) {
						// console.log(titleCase(diffLabel));
						let diff: BeatmapDifficulty = (<any>BeatmapDifficulty)[
							titleCase(diffLabel)
						];
						diffs.push(diff);
					}
				}
			}
			characteristics.push({
				SerializedName: characteristic.name,
				Difficulties: diffs,
			});
		}
		SelectedCharacteristic = characteristics[0];
		// console.log(characteristics);
		let matchMap: PreviewBeatmapLevel = {
			LevelId: `custom_level_${hash.toUpperCase()}`,
			Name: songData.metadata.songName,
			Characteristics: characteristics,
			Loaded: true,
		};
		selectedLevel = matchMap;
		loading = false;
	}

	function titleCase(str): string {
		var splitStr = str.split(" ");
		for (var i = 0; i < splitStr.length; i++) {
			splitStr[i] =
				splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
		}
		return splitStr.join(" ");
	}

	function addSong() {
        let gm: GameplayModifiers = { Options: GameOptions.None };
		for (const modifier of mapOptions) {
			if (modifier.isSelected) {
				gm.Options = gm.Options | modifier.value;
			}
		}
		let map: GameplayParameters = {
			Beatmap: {
				Name: selectedLevel.Name,
				LevelId: selectedLevel.LevelId,
				Characteristic: SelectedCharacteristic,
				Difficulty: selectedDiff,
			},
			PlayerSettings: {
				Options: 0,
			},
			GameplayModifiers: gm,
		};
		event.QualifierMaps.push(map);
		let SpecificPacket: TAEvent = {
			Type: EventType.QualifierEventUpdated,
			ChangedObject: event,
		};
		onOkay(SpecificPacket);
		close();
	}

	$: validDiff = SelectedCharacteristic?.Difficulties?.includes(selectedDiff);

	function updateSelection() {}

	let loading = false;
</script>

<h2>Add Song</h2>
<CustomInput bind:value={bsURL} placeHolder="Song URL/ID" />
<CustomButton
	on:click={loadSong}
	primary
	disabled={loading || bsURL == "" || !bsURL}
	text={loading ? "Loading Song" : "Load Song"}
/>
<br />
{#if selectedLevel}
	<Select bind:value={SelectedCharacteristic}>
		{#each selectedLevel?.Characteristics as char}
			<option value={char}>{char.SerializedName}</option>
		{/each}
	</Select>
{/if}
{#if SelectedCharacteristic}
	<Select bind:value={selectedDiff}>
		<option selected={!validDiff} disabled> Select a Difficulty </option>
		{#each SelectedCharacteristic?.Difficulties.sort() as diff}
			<option selected={diff == selectedDiff} value={diff}>
				{BeatmapDifficulty[diff]}
			</option>
		{/each}
	</Select>
{/if}
{#if SelectedCharacteristic && selectedDiff}
	<br /><br />
    Options:
    <br>
	<ul style="list-style:none;">
		{#each mapOptions as option}
			<li>
				<CustomCheckbox
					bind:checked={option.isSelected}
					label={option.name}
				/>
			</li>
		{/each}
	</ul>
	<br /><br />
	<CustomButton on:click={addSong} primary text={"Add Song"} />
{/if}

<br /><br />
<CustomButton danger on:click={close} text="Close" />

<style>
</style>
