<script lang="ts">
    import Cookies from "js-cookie";
import CustomButton from "../components/customButton.svelte";
    import CustomInput from "../components/customInput.svelte";

    let ss = Cookies.get("UserId") || "";

    function updateUserId() {
        if(ss.includes("scoresaber.com")) {
            ss = ss.split('/u/')[1].replace(/\D+/g, '');
        }
        Cookies.set("UserId", ss, { expires: 365 });
        console.log(ss);
    }

    function removeUserId() {
        Cookies.remove("UserId");
    }
</script>

<style>

</style>

<h3>Settings</h3>
<CustomInput placeHolder="Score Saber Link" bind:value={ss}/>
<CustomButton on:click={() => updateUserId()} text="Update" primary />
<CustomButton on:click={() => removeUserId()} text="Remove" danger />
<br>
Themes soon TM