<script lang="ts">
	import { createEventDispatcher } from "svelte";
	import CustomButton from "../components/customButton.svelte";
	import { Packet, PacketType } from "../components/packet";
	import { EventType, TAEvent } from "../models/event";
	import type { GameplayParameters } from "../models/gameplayParameters";
	import { BeatmapDifficulty } from "../models/match";

	import type { QualifierEvent } from "../models/qualifierEvent";
import type { ScoreRequest } from "../models/scoreRequest";
	import AddSong from "../_modals/addSong.svelte";

	export let modal;
	export let curEvent: QualifierEvent;
	const dispatch = createEventDispatcher();

	const onOkay = (specificPacket) => {
		if (specificPacket) {
			dispatch(
				"sendPacket",
				new Packet(specificPacket, PacketType.Event)
			);
		}
	};

	function addSong() {
		modal.open(
			AddSong,
			{ event: curEvent, onOkay },
			{},
			{
				onClose: () => {
					// dispMatch.addScore(levelCompletionResults);
					// levelCompletionResults = [];
				},
			}
		);
	}

	function delSong(song: GameplayParameters) {
		let index = curEvent.QualifierMaps.findIndex(
			(x) => x.Beatmap.LevelId == song.Beatmap.LevelId
		);
		if (index > -1) {
			curEvent.QualifierMaps.splice(index, 1);
			let SpecificPacket: TAEvent = {
				Type: EventType.QualifierEventUpdated,
				ChangedObject: curEvent,
			};
			dispatch(
				"sendPacket",
				new Packet(SpecificPacket, PacketType.Event)
			);
		}
	}
	
	function eventScoreRequest(event: QualifierEvent, song: GameplayParameters) {
		let SpecificPacket: ScoreRequest = {
			EventId: event.EventId,
			Parameters: song
		};
		dispatch(
			"sendPacket",
			new Packet(SpecificPacket, PacketType.ScoreRequest)
		);
		// ws.sendMessage(
		// 	JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
		// );
	}
    
    function deleteEvent(event) {
        dispatch(
				"delEvent",
				event
			);
    }
</script>

<!-- svelte-ignore a11y-invalid-attribute -->
<a
	on:click={() => {
		dispatch("back");
	}}
	href="javascript:void();"> &#8592; Back </a>
<h1>{curEvent.Name}</h1>
<hr />
<h3>Maps</h3>
<div class="cards" style="max-width: 200px;">
	{#each curEvent.QualifierMaps as map}
		<div
			class="infoCard noHover"
			style="--bgImg: url(https://scoresaber.com/imports/images/songs/{map.Beatmap.LevelId.replace(
				'custom_level_',
				''
			)}.png);"
		>
			<p style="padding-left: 0px;">
				{map.Beatmap.Name} -
				<span
					class={BeatmapDifficulty[map.Beatmap.Difficulty].replace(
						"Plus",
						"P"
					)}
					>{BeatmapDifficulty[map.Beatmap.Difficulty].replace(
						"Plus",
						"+"
					)}</span
				>
			</p>
			<CustomButton on:click={() => delSong(map)} danger text="Delete" />
			<CustomButton on:click={() => eventScoreRequest(curEvent,map)} secondary text="Get Scores"/>
		</div>
	{/each}
</div>
<!-- <ul>
	{#each curEvent.QualifierMaps as map}
		<li>
			{map.Beatmap.Name}
		</li>
	{/each}
</ul> -->
<br />
<CustomButton secondary on:click={addSong} text="Add Song" />
<CustomButton on:click={() => deleteEvent(curEvent)} danger text="Delete Event" />

