	.infoCard {
		&::before {
			content: " ";
			background-repeat: no-repeat;
			background-size: cover;
			background-image: var(--bgImg);
			opacity: 0.1;
			pointer-events: none;
			position: absolute;
			top: 0px;
			bottom: 0px;
			left: 0px;
			right: 0px;
		}
	}

	.ExpertP {
		color: #8f48db;
	}

	.Expert {
		color: #bf2a42;
	}

	.Hard {
		color: #ff6347;
	}

	.Normal {
		color: #59b0f4;
	}

	.Easy {
		color: #3cb371;
	}
</style>
