<script lang="ts">
	import { getContext } from "svelte";
	import CustomButton from "../components/customButton.svelte";
	import { TAEvent } from "../models/event";
	import type { SongFinished } from "../models/songFinished";
	import { CompletionType } from "../models/songFinished";
	import type { State } from "../models/state";
	import type { teamScores } from "../models/team";

	export let results: SongFinished[] = [];
	export let state: State;
	let teams = <teamScores[]>state?.ServerSettings?.Teams;

	results.sort((a, b) => {
		return b.Score - a.Score;
	});

	const { close } = getContext("simple-modal");

	function getTypeString(val: CompletionType) {
		return CompletionType[val];
	}

	if (state?.ServerSettings?.EnableTeams) {
		for (const team of teams) {
			let teamIndex = teams.findIndex((x) => x.Id == team.Id);
			if (teamIndex > -1) {
				let score = 0;
				let acc = 0;
				let userCount = 0;
				for (const result of results) {
					if (result.User.Team.Id == team.Id) {
						score += result.User.Score;
						acc += result.User.Accuracy;
						userCount++;
					}
				}
				if (userCount > 0) {
					acc /= userCount;
					teams[teamIndex].Score = score;
					teams[teamIndex].Accuracy = acc;
					teams = teams;
				}
			}
		}
	}
</script>

<h2>Results</h2>
{#if state.ServerSettings.EnableTeams}
	<p>Teams</p>
	<ol>
		{#each teams as team}
			{#if team.Score}
				<li>
					{team.Name}
					<br />
					<div style="margin-left:10px">
						Score:
						{team.Score}
						<br />
						Avg Acc:
						{(team.Accuracy * 100).toFixed(2)}%
					</div>
				</li>
			{/if}
		{/each}
	</ol>
{/if}
<p>Players</p>
<ol>
	{#each results.sort() as result}
		<li>
			{result.User.Name}
			<br />
			<div style="margin-left:10px">
				Score:
				{result.User.Score}
				<br />
				Acc:
				{(result.User.Accuracy * 100).toFixed(2)}%
				<br />
				Result:
				{getTypeString(result.Type)}
			</div>
		</li>
	{/each}
</ol>
<br />
<CustomButton danger on:click={close} text="Close" />

<style>
</style>
