<script lang="ts">
	import { wsHandler } from "./components/wsHandler";
	import { onMount, onDestroy, getContext } from "svelte";
	import { client } from "./components/client";
	import { ConnectTypes } from "./models/connect";
	import { Packet, PacketType } from "./components/packet";
	import type { ConnectResponse } from "./models/connectResponse";
	import { EventType, TAEvent } from "./models/event";
	import { BeatmapDifficulty } from "./models/match";
	import type { Match } from "./models/match";
	import type { Player } from "./models/player";
	import type { SongFinished } from "./models/songFinished";
	import { v4 as uuidv4 } from "uuid";
	import DisplayMatch from "./components/displayMatch.svelte";
	import CustomInput from "./components/customInput.svelte";
	import CustomButton from "./components/customButton.svelte";
	import Modal from "./components/modal.svelte";
	import SongFin from "./_modals/songFin.svelte";
	import Progress from "./components/Progress.svelte";

	import Cookies from "js-cookie";
	import Settings from "./_modals/Settings.svelte";
	import type { ScoreRequest } from "./models/scoreRequest";
	import type { QualifierEvent } from "./models/qualifierEvent";
	import EventPage from "./pages/eventPage.svelte";
import type { teamScores } from "./models/team";

	let address = Cookies.get("HostIP") || "127.0.0.1:10151";

	let ws: wsHandler;

	let taClient: client = new client();

	// let wsConnected = false;

	let selectedPlayers: Player[] = [];

	$: wsConnected = ws?.socket?.readyState;

	onDestroy(() => {
		disconect();
	});

	window.addEventListener("beforeunload", function (evt) {
		if (taClient.Self) disconect();
	});

	let name = "";
	let password = "";
	function connect() {
		if (name == "") {
			alert("Please enter a name");
			return null;
		}
		ws = new wsHandler(address);
		Cookies.set("HostIP", address, { expires: 365 });
		// console.log(ws.socket.readyState);
		ws.socket.onopen = () => {
			wsConnected = ws.socket.readyState;
			let packetData = {
				ClientType: ConnectTypes.Coordinator,
				Name: name,
				UserId: Cookies.get("UserId") || "0",
				ClientVersion: 36,
				Password: password,
			};
			let packet = new Packet(packetData, PacketType.Connect);
			ws.sendMessage(JSON.stringify(packet));
		};
		ws.socket.onclose = () => (wsConnected = ws.socket.readyState);
		ws.socket.onerror = () => (wsConnected = ws.socket.readyState);
		ws.subscribe((message) => {
			try {
				handlePacket(JSON.parse(message));
			} catch (error) {
				// console.error(error);
			}
		});
	}
	
	$: teams = <teamScores[]>taClient.State?.ServerSettings?.Teams;

	function handlePacket(packet) {
		// console.log(packet);
		if (packet.Type == PacketType.ConnectResponse) {
			let connectResponse = packet.SpecificPacket as ConnectResponse;
			if (!taClient.Self) {
				taClient = new client(connectResponse);
				// console.log(taClient.State);
			}
		} else if (packet.Type == PacketType.Event && taClient.isConnected) {
			let event = packet.SpecificPacket as TAEvent;
			switch (event.Type) {
				case EventType.CoordinatorAdded:
					taClient.coordinatorAdded(event.ChangedObject);
					break;
				case EventType.CoordinatorLeft:
					taClient.coordinatorLeft(event.ChangedObject);
					break;
				case EventType.MatchCreated:
					var match: Match = event.ChangedObject;
					if (match.Guid == tmpMatch?.Guid) {
						curMatch = match;
						tmpMatch = undefined;
					}
					taClient.matchCreated(match);
					break;
				case EventType.MatchUpdated:
					var match: Match = event.ChangedObject;
					if (match.Guid == curMatch?.Guid) curMatch = match;
					taClient.matchUpdated(event.ChangedObject);
					break;
				case EventType.MatchDeleted:
					let matchDel: Match = event.ChangedObject;
					if (
						matchDel.Guid == curMatch?.Guid &&
						matchDel.Guid != tmpMatch.Guid
					)
						curMatch = null;
					taClient.matchDeleted(event.ChangedObject);
					break;
				case EventType.PlayerAdded:
					taClient.playerAdded(event.ChangedObject);
					break;
				case EventType.PlayerUpdated:
					let player: Player = event.ChangedObject;
					// console.log(player);
					taClient.playerUpdated(event.ChangedObject);
					if (curMatch?.Players.find((x) => x.Id == player.Id)) {
						curMatch.Players[
							curMatch?.Players.findIndex(
								(x) => x.Id == player.Id
							)
						] = player;
						curMatch = curMatch;
					}
					// console.log(taClient);
					updatePlayer(player);
					break;
				case EventType.PlayerLeft:
					taClient.playerLeft(event.ChangedObject);
					break;
				case EventType.QualifierEventCreated:
					taClient.qualifierEventCreated(event.ChangedObject);
					break;
				case EventType.QualifierEventUpdated:
					let qualEvent: QualifierEvent = event.ChangedObject;
					if (qualEvent.EventId == selEvent.EventId)
						selEvent = qualEvent;
					taClient.qualifierEventUpdated(event.ChangedObject);
					break;
				case EventType.QualifierEventDeleted:
					let delQualEvent: QualifierEvent = event.ChangedObject;
					if (delQualEvent.EventId == selEvent.EventId)
						selEvent = null;
					taClient.qualifierEventDeleted(event.ChangedObject);
					break;
				default:
					break;
			}
		} else if (
			packet.Type == PacketType.SongFinished &&
			taClient.isConnected &&
			curMatch
		) {
			let songFin = packet.SpecificPacket as SongFinished;
			console.log(songFin);
			// if (songFin)
			songFinished(songFin);
			if (levelCompletionResults.length == curMatch.Players.length) {
				songComplete();
			}
		} else if (packet.Type == PacketType.ForwardingPacket) {
			handlePacket(packet.SpecificPacket);
		}
		taClient = taClient;
		// console.log(taClient);
	}

	let levelCompletionResults: SongFinished[] = [];

	function songFinished(songFin: SongFinished) {
		let index = curMatch?.Players.findIndex((x) => x.Id == songFin.User.Id);
		if (
			index > -1 &&
			!levelCompletionResults.find((x) => x.User.Id == songFin.User.Id)
		) {
			if (songFin.Beatmap.LevelId == curMatch.SelectedLevel.LevelId) {
				songFin.Beatmap.Name = curMatch.SelectedLevel.Name;
			}
			levelCompletionResults.push(songFin);
		}
	}

	let modal;
	let dispMatch: DisplayMatch;

	function songComplete() {
		modal.open(
			SongFin,
			{ results: levelCompletionResults, state: taClient.State },
			{},
			{
				onClose: () => {
					dispMatch.addScore(levelCompletionResults);
					levelCompletionResults = [];
				},
			}
		);
	}

	function updatePlayer(player: Player) {
		let tmpPlayer = curMatch.Players.find((x) => x.Id == player.Id);
		animateValue(player, "Score", tmpPlayer.Score, player.Score, 500);
		animateValue(
			player,
			"Accuracy",
			tmpPlayer.Accuracy,
			player.Accuracy,
			500
		);
		animateValue(player, "Combo", tmpPlayer.Combo, player.Combo, 500);
	}

	

	function animateValue(player: Player, property, start, end, duration) {
		let startTimestamp = null;
		const step = (timestamp) => {
			if (!startTimestamp) startTimestamp = timestamp;
			const progress = Math.min(
				(timestamp - startTimestamp) / duration,
				1
			);
			if (property == "Accuracy") {
				player[property] = progress * (end - start) + start;
			} else {
				player[property] = Math.floor(progress * (end - start) + start);
			}
			curMatch.Players[
				curMatch.Players.findIndex((x) => x.Id == player.Id)
			] = player;
			curMatch = curMatch;
			if (taClient.State.ServerSettings.EnableTeams) {
				updateTeam(player.Team.Id);
			}
			// obj.innerHTML = Math.floor(progress * (end - start) + start);
			if (progress < 1) {
				window.requestAnimationFrame(step);
			}
		};
		window.requestAnimationFrame(step);
	}

	function updateTeam(teamId) {
		let teamIndex = teams.findIndex(x => x.Id == teamId);
		if (teamIndex > -1) {
			let score = 0;
			let acc = 0;
			let userCount = 0;
			for (const user of curMatch.Players) {
				if (user.Team.Id == teamId) {
					score += user.Score
					acc += user.Accuracy;
					userCount++;
				}
			}
			if (userCount > 0) {
				acc /= userCount;
				teams[teamIndex].Score = score;
				teams[teamIndex].Accuracy = acc;
				teams = teams;
			}
		} 
	}

	function disconect() {
		let SpecificPacket: TAEvent = {
			Type: EventType.CoordinatorLeft,
			ChangedObject: taClient.Self,
		};
		ws.sendMessage(
			JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
		);
		// setTimeout(() => {
		// }, 1000);
		ws.socket.close();
		ws = null;
		taClient = new client();
		// ws.close();
	}

	function updateSelect(player: Player) {
		let playerIndex = selectedPlayers.findIndex((x) => x.Id == player.Id);
		if (playerIndex == -1) {
			selectedPlayers = [...selectedPlayers, player];
		} else {
			selectedPlayers = [
				...selectedPlayers.slice(0, playerIndex),
				...selectedPlayers.slice(playerIndex + 1),
			];
		}
	}

	let curMatch: Match;

	function createMatch() {
		let match: Match = {
			Guid: uuidv4(),
			Players: selectedPlayers,
			Leader: taClient.Self,
			SelectedDifficulty: 0,
		};
		curMatch = match;
		let SpecificPacket: TAEvent = {
			Type: EventType.MatchCreated,
			ChangedObject: match,
		};
		ws.sendMessage(
			JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
		);
		selectedPlayers = [];
	}

	function deleteMatch() {
		let SpecificPacket: TAEvent = {
			Type: EventType.MatchDeleted,
			ChangedObject: curMatch,
		};
		ws.sendMessage(
			JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
		);
		curMatch = null;
	}

	let tmpMatch: Match;

	function kickPlayer(event) {
		let player = event.detail;
		let pIndex = curMatch.Players.findIndex((x) => x.Id == player.Id);
		if (pIndex > -1) {
			curMatch.Players.splice(pIndex, 1);
			if (curMatch.Players.length < 1) {
				deleteMatch();
				return;
			}

			tmpMatch = curMatch;
			let SpecificPacket: TAEvent = {
				Type: EventType.MatchDeleted,
				ChangedObject: curMatch,
			};
			ws.sendMessage(
				JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
			);
			setTimeout(() => {
				let match: Match = {
					Guid: tmpMatch.Guid,
					Players: tmpMatch.Players,
					Leader: tmpMatch.Leader,
					SelectedDifficulty: 0,
				};
				// curMatch = match;
				SpecificPacket = {
					Type: EventType.MatchCreated,
					ChangedObject: match,
				};
				ws.sendMessage(
					JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
				);
			}, 1000);
		}
	}

	function setMatch(match: Match) {
		curMatch = match;
	}

	function sendPacket(event) {
		ws.sendMessage(JSON.stringify(event.detail));
	}

	function playerInMatch(player: Player): boolean {
		for (const match of taClient.State.Matches) {
			if (match.Players.find((x) => x.Id == player.Id)) {
				return true;
			}
		}
		return false;
	}

	function openSettings() {
		modal.open(Settings);
	}

	function imgErr(event) {
		this.src = "https://scoresaber.com/imports/images/oculus.png";
	}

	function j2s(object) {
		return JSON.stringify(object, null, 4);
	}

	function createEvent(name: string) {
		let qualEvent: QualifierEvent = {
			EventId: uuidv4(),
			Name: name,
			Guild: {
				Id: "000000000000000000",
				Name: "WS-Generated",
			},
			InfoChannel: {
				Id: "000000000000000000",
				Name: "WS-Generated",
			},
			QualifierMaps: [],
			SendScoresToInfoChannel: false,
			Flags: 0,
		};
		let SpecificPacket: TAEvent = {
			Type: EventType.QualifierEventCreated,
			ChangedObject: qualEvent,
		};
		ws.sendMessage(
			JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
		);
		eventName = "";
	}

	function deleteEvent(event) {
		console.log(event.detail);
		let SpecificPacket: TAEvent = {
			Type: EventType.QualifierEventDeleted,
			ChangedObject: event.detail,
		};
		ws.sendMessage(
			JSON.stringify(new Packet(SpecificPacket, PacketType.Event))
		);
	}

	let eventName = "";
	let selEvent: QualifierEvent = null;
</script>

<main>
	<span class="menu" on:click={openSettings}> &#9881; </span>
	{#if curMatch}
		<DisplayMatch
			{curMatch}
			on:close={deleteMatch}
			on:sendPacket={sendPacket}
			on:playerKick={kickPlayer}
			bind:this={dispMatch}
			taState={taClient.State}
			teams={teams}
			{modal}
		/>
	{:else if selEvent != null}
		<EventPage
			curEvent={selEvent}
			on:back={() => {
				selEvent = null;
			}}
			on:sendPacket={sendPacket}
			on:delEvent={deleteEvent}
			{modal}
		/>
	{:else if taClient.isConnected}
		<h1>Welcome {taClient.Self.Name}</h1>
		<div>
			<div>
				<h3>Coordinators</h3>
				<div class="cards">
					{#each taClient.State.Coordinators as coord}
						{#if coord.UserId != "0"}
							<div
								class="infoCard coordCard"
								style={coord.Id == taClient.Self.Id
									? "order: -1;"
									: ""}
							>
								<img
									src="https://new.scoresaber.com/api/static/avatars/{coord.UserId}.jpg"
									on:error={imgErr}
									alt=""
								/>
								<p>
									{#if coord.Id == taClient.Self.Id}
										<b>{coord.Name}</b>
									{:else}{coord.Name}{/if}
								</p>
							</div>
						{:else}
							<div
								class="infoCard"
								style={coord.Id == taClient.Self.Id
									? "order: -1;"
									: ""}
							>
								<p>
									{#if coord.Id == taClient.Self.Id}
										<b>{coord.Name}</b>
									{:else}{coord.Name}{/if}
								</p>
							</div>
						{/if}
					{/each}
				</div>
			</div>

			<div>
				<h3>Players</h3>
				<div class="cards">
					{#each taClient.State.Players.filter((x) => !playerInMatch(x)) as player}
						<div
							class="infoCard playerCard clickable"
							class:selected={selectedPlayers.find(
								(x) => x.Id == player.Id
							)}
							on:click={() => updateSelect(player)}
							style="background-image: url('https://new.scoresaber.com/api/static/avatars/{player.UserId}.jpg');"
						>
							<!-- <img
								loading="lazy"
								class="playerImg"
								src="https://new.scoresaber.com/api/static/avatars/{player.UserId}.jpg"
								alt="{player.Name}'s profile image'" /> -->
							<p>{player.Name}</p>
						</div>
					{/each}
				</div>
				<br />
				{#if selectedPlayers.length > 0}
					<CustomButton
						on:click={createMatch}
						secondary
						text="Create Match"
					/>
				{/if}
			</div>

			<div>
				<h3>Matches</h3>
				<div class="cards">
					{#each taClient.State.Matches as match}
						<div
							class="infoCard clickable"
							on:click={() => setMatch(match)}
						>
							<p>{match.Leader.Name}'s Match</p>
							<hr />
							<br />
							Players
							<br />
							<ul class="playerList">
								{#each match.Players as player}
									<li>
										<img
											loading="lazy"
											class="playerImg mini"
											src="https://new.scoresaber.com/api/static/avatars/{player.UserId}.jpg"
											on:error={imgErr}
											alt="{player.Name}'s profile image'"
										/>
										{player.Name}
									</li>
								{/each}
							</ul>
						</div>
					{/each}
				</div>
			</div>
		</div>
		<div>
			<h3>Events</h3>
			<div class="cards">
				{#each taClient.State.Events as event}
					<div
						class="infoCard clickable"
						on:click={() => {
							selEvent = event;
						}}
					>
						<p style="padding-left: 0px;">
							<b>Event: </b>
							{event.Name}
						</p>

						<!-- <CustomButton
							on:click={() => eventScoreRequest(event)}
							secondary
							text="request scores"
						/> -->
					</div>
				{/each}
			</div>
			<br />
			<CustomInput bind:value={eventName} placeHolder="Event Name" />
			<CustomButton
				on:click={() => createEvent(eventName)}
				secondary
				text="Create Event"
				disabled={eventName == ""}
			/>
		</div>
		<CustomButton on:click={disconect} secondary text="Disconect" />
		<!-- <br /><br />
		<pre>
			{ j2s(taClient) }
		</pre> -->
	{:else if wsConnected == 0}
		<p>Connecting to TA server...</p>
	{:else}
		<p>
			This is an experimental TA client and will only work with comptatble
			servers. <br />
			Send Dannypoke03#3401 a dm on discord if you wish to use this for your
			tournament.
		</p>
		<CustomInput bind:value={address} placeHolder="Host IP" />
		<CustomInput bind:value={name} placeHolder="Username" />
		<CustomInput bind:value={password} placeHolder="Password" />
		<CustomButton
			on:click={connect}
			disabled={name == ""}
			primary
			text="Connect"
		/>
	{/if}
</main>
<svelte:component this={Modal} bind:this={modal} />

