import { ResponseType } from "../models/response";
export class client {
    constructor(connectResponse) {
        this.isConnected = false;
        this.matchId = false;
        if (connectResponse && connectResponse.Type == ResponseType.Success) {
            this.Self = connectResponse.Self;
            this.State = connectResponse.State;
            this.isConnected = true;
        }
    }
    coordinatorAdded(data) {
        if (!this.isConnected)
            return;
        let index = this.State.Coordinators.findIndex(x => x.Id == data.Id);
        if (index == -1)
            this.State.Coordinators.push(data);
    }
    coordinatorLeft(data) {
        let index = this.State.Coordinators.findIndex(x => x.Id == data.Id);
        if (index > -1)
            this.State.Coordinators.splice(index, 1);
    }
    matchCreated(match) {
        // console.log(match);
        if (!this.State.Matches.find(x => x.Guid == match.Guid))
            this.State.Matches.push(match);
    }
    matchUpdated(match) {
        let index = this.State.Matches.findIndex(x => x.Guid == match.Guid);
        if (index > -1)
            this.State.Matches[index] = match;
    }
    matchDeleted(match) {
        let index = this.State.Matches.findIndex(x => x.Guid == match.Guid);
        if (index > -1)
            this.State.Matches.splice(index, 1);
    }
    playerAdded(player) {
        this.State.Players.push(player);
    }
    playerUpdated(player) {
        let index = this.State.Players.findIndex(x => x.Id == player.Id);
        console.log(index);
        if (index > -1)
            this.State.Players[index] = player;
    }
    playerLeft(player) {
        let index = this.State.Players.findIndex(x => x.Id == player.Id);
        if (index > -1)
            this.State.Players.splice(index, 1);
    }
    qualifierEventCreated(event) {
        let index = this.State.Events.findIndex(x => x.EventId == event.EventId);
        if (index == -1)
            this.State.Events.push(event);
    }
    qualifierEventUpdated(event) {
        let index = this.State.Events.findIndex(x => x.EventId == event.EventId);
        if (index > -1)
            this.State.Events[index] = event;
    }
    qualifierEventDeleted(event) {
        let index = this.State.Events.findIndex(x => x.EventId == event.EventId);
        if (index > -1)
            this.State.Events.splice(index, 1);
    }
}
