<script lang="ts">
	import { BeatmapDifficulty } from "../models/match";
	import type { Match } from "../models/match";
	import { createEventDispatcher } from "svelte";
	import type { Characteristic } from "../models/characteristic";
	import type { PreviewBeatmapLevel } from "../models/previewBeatmapLevel";
	import type { LoadSong } from "../models/loadSong";
	import type { ForwardingPacket } from "../models/forwardingPacket";
	import { Packet, PacketType } from "./packet";
	import { EventType, TAEvent } from "../models/event";
	import {
		GameOptions,
		GameplayModifiers,
	} from "../models/gameplayModifiers";
	import type { Beatmap } from "../models/beatmap";
	import type { GameplayParameters } from "../models/gameplayParameters";
	import { PlayerOptions } from "../models/playerSpecificSettnigs";
	import type { PlaySong } from "../models/playSong";
	import { Command, CommandTypes } from "../models/command";
	import CustomInput from "./customInput.svelte";
	import CustomButton from "./customButton.svelte";
	import { DownloadStates, PlayStates } from "../models/player";
	import type { Player } from "../models/player";
	import CustomCheckbox from "./customCheckbox.svelte";
	import Select from "./Select.svelte";
	import { CompletionType } from "../models/songFinished";
	import type { SongFinished } from "../models/songFinished";
	import Accorian from "./Accorian.svelte";
	import AccordianTab from "./AccordianTab.svelte";
	import Progress from "./Progress.svelte";

	export let modal;
	export let taState: State;
	export let curMatch: Match;
	export let teams: teamScores[];
	const dispatch = createEventDispatcher();

	let previousScores: SongFinished[][] = [];

	export const addScore = (score: SongFinished[]) => {
		previousScores = [...previousScores, score];
	};

	let bsURL: string;

	let bannedCheck = false;

	let taSongOptions = [
		{
			label: "Show Scoreboard",
			isSelected: false,
		},
		{
			label: "Disable Pause",
			isSelected: true,
		},
		{
			label: "Disable Fail",
			isSelected: true,
		},
	];

	let mapOptions = [];
	for (const modifier in GameOptions) {
		if (isNaN(Number(modifier))) {
			if (modifier == "None") continue;
			mapOptions.push({
				name: modifier.replace(/([A-Z])/g, " $1").trim(),
				value: GameOptions[modifier],
				isSelected: false,
			});
		}
	}

	async function loadSong() {
		if (bsURL == "" || !bsURL) return null;
		let songId = bsURL;
		if (bsURL.includes("beatsaver")) {
			// return null;
			songId = bsURL.split("beatmap/")[1];
			if (songId.includes("&")) songId = songId.split("&")[0];
		}
		let req = await fetch(
			`https://beatsaver.com/api/maps/detail/${songId}`,
			{
				method: "GET",
				headers: {
					"User-Agent":
						"TAClient/1.0.0 (+https://github.com/Dannypoke03)",
				},
			}
		);
		let songData = await req.json();
		let hash: string = songData.hash;
		let characteristics: Characteristic[] = [];
		curMatch.SelectedDifficulty = 0;
		// console.log(songData);
		for (const characteristic of songData.metadata.characteristics) {
			let diffs: BeatmapDifficulty[] = [];
			for (const diffLabel in characteristic.difficulties) {
				if (
					Object.prototype.hasOwnProperty.call(
						characteristic.difficulties,
						diffLabel
					)
				) {
					if (characteristic.difficulties[diffLabel] != null) {
						// console.log(titleCase(diffLabel));
						let diff: BeatmapDifficulty = (<any>BeatmapDifficulty)[
							titleCase(diffLabel)
						];
						diffs.push(diff);
					}
				}
			}
			characteristics.push({
				SerializedName: characteristic.name,
				Difficulties: diffs,
			});
		}
		// console.log(characteristics);
		let matchMap: PreviewBeatmapLevel = {
			LevelId: `custom_level_${hash.toUpperCase()}`,
			Name: songData.metadata.songName,
			Characteristics: characteristics,
			Loaded: true,
		};

		curMatch.SelectedLevel = matchMap;
		curMatch.SelectedCharacteristic = matchMap.Characteristics[0];
		curMatch.SelectedDifficulty = BeatmapDifficulty.Easy;

		// dispatch('matchUpdate', curMatch);
		let SpecificPacket: TAEvent = {
			Type: EventType.MatchUpdated,
			ChangedObject: curMatch,
		};

		// this.socket.emit("updateMatch", curMatch);

		let loadedSong: LoadSong = {
			LevelId: curMatch.SelectedLevel.LevelId,
			CustomHostUrl: null,
		};
		let playerIds = curMatch.Players.map((x) => x.Id);
		let specificPacket2: ForwardingPacket = {
			ForwardTo: playerIds,
			Type: PacketType.LoadSong,
			SpecificPacket: loadedSong,
		};

		setTimeout(function () {
			dispatch(
				"sendPacket",
				new Packet(specificPacket2, PacketType.ForwardingPacket)
			);
		}, 1000);

		dispatch("sendPacket", new Packet(SpecificPacket, PacketType.Event));
		bannedCheck = false;
	}

	function titleCase(str): string {
		var splitStr = str.split(" ");
		for (var i = 0; i < splitStr.length; i++) {
			splitStr[i] =
				splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
		}
		return splitStr.join(" ");
	}

	$: allFinLoad = !curMatch.Players.every(
		(x) => x.DownloadState != DownloadStates.None
	);
	$: allWait = curMatch.Players.every(
		(x) => x.PlayState == PlayStates.Waiting
	);
	$: allInGame = curMatch.Players.every((x) => x.PlayState == 1);
	$: playersDownloading = curMatch.Players.some(
		(x) => x.DownloadState == DownloadStates.Downloading
	);
	$: playersDownloaded = curMatch.Players.every(
		(x) => x.DownloadState == DownloadStates.Downloaded
	);

	$: validDiff = curMatch.SelectedCharacteristic?.Difficulties?.includes(
		curMatch.SelectedDifficulty
	);
	$: canPlay =
		playersDownloaded &&
		allWait &&
		curMatch.SelectedCharacteristic?.Difficulties?.includes(
			curMatch.SelectedDifficulty
		);

	function updateMatch() {
		let SpecificPacket: TAEvent = {
			Type: EventType.MatchUpdated,
			ChangedObject: curMatch,
		};
		dispatch("sendPacket", new Packet(SpecificPacket, PacketType.Event));
	}

	function playSong() {
		let gm: GameplayModifiers = { Options: GameOptions.None };
		for (const modifier of mapOptions) {
			if (modifier.isSelected) {
				gm.Options = gm.Options | modifier.value;
			}
		}
		let beatMap: Beatmap = {
			Characteristic: curMatch.SelectedCharacteristic,
			Difficulty: curMatch.SelectedDifficulty,
			LevelId: curMatch.SelectedLevel.LevelId,
			Name: curMatch.SelectedLevel.Name,
		};
		let gameplayParam: GameplayParameters = {
			PlayerSettings: {
				Options: PlayerOptions.None,
			},
			GameplayModifiers: gm,
			Beatmap: beatMap,
		};

		let playSong: PlaySong = {
			GameplayParameters: gameplayParam,
			FloatingScoreboard: taSongOptions[0].isSelected,
			StreamSync: false,
			DisablePause: taSongOptions[1].isSelected,
			DisableFail: taSongOptions[2].isSelected,
		};
		let playerIds = curMatch.Players.map((x) => x.Id);
		let specificPacket: ForwardingPacket = {
			ForwardTo: playerIds,
			Type: PacketType.PlaySong,
			SpecificPacket: playSong,
		};
		dispatch(
			"sendPacket",
			new Packet(specificPacket, PacketType.ForwardingPacket)
		);
	}

	function returnToMenu() {
		let playerIds = curMatch.Players.map((x) => x.Id);
		let specificPacket: ForwardingPacket = {
			ForwardTo: playerIds,
			Type: PacketType.Command,
			SpecificPacket: {
				commandType: CommandTypes.ReturnToMenu,
			},
		};
		dispatch(
			"sendPacket",
			new Packet(specificPacket, PacketType.ForwardingPacket)
		);
	}

	function checkBanned() {
		let players = [];
		for (const player of curMatch.Players) {
			let bannedMods = [];
			for (const mod of player.ModList) {
				if (taState.ServerSettings.BannedMods.includes(mod)) {
					bannedMods.push(mod);
				}
			}
			players.push({
				player: player,
				bannedMods: bannedMods,
			});
		}
		modal.open(
			ModCheck,
			{ players: players },
			{},
			{
				onClose: () => {
					// dispMatch.addScore(levelCompletionResults);
					// levelCompletionResults = [];
					bannedCheck = true;
				},
			}
		);
	}

	function checkWhiteList() {
		let players = [];
		for (const player of curMatch.Players) {
			let bannedMods = [];
			for (const mod of player.ModList) {
				if (!taState.ServerSettings.BannedMods.includes(mod)) {
					bannedMods.push(mod);
				}
			}
			players.push({
				player: player,
				bannedMods: bannedMods,
			});
		}
		modal.open(
			ModCheck,
			{ players: players },
			{},
			{
				onClose: () => {
					bannedCheck = true;
				},
			}
		);
	}

	function kickFromMatch(player: Player) {
		dispatch("playerKick", player);
	}

	function getTypeString(val: CompletionType) {
		return CompletionType[val];
	}

	import { quintOut } from "svelte/easing";
	import { crossfade } from "svelte/transition";
	import { flip } from "svelte/animate";
	import type { State } from "../models/state";
	import ModCheck from "../_modals/modCheck.svelte";
	import type { teamScores } from "../models/team";

	const [send, receive] = crossfade({
		duration: (d) => Math.sqrt(d * 200),

		fallback(node, params) {
			const style = getComputedStyle(node);
			const transform = style.transform === "none" ? "" : style.transform;

			return {
				duration: 600,
				easing: quintOut,
				css: (t) => `
					transform: ${transform} scale(${t});
					opacity: ${t}
				`,
			};
		},
	});

	function imgErr(event) {
		this.src = "https://scoresaber.com/imports/images/oculus.png";
	}

</script>

<section>
	<div style="min-width: 30%">
		<h3>Players</h3>
		{#if !allInGame}
			<CustomButton
				text="Check for Banned Mods"
				secondary
				on:click={checkBanned}
			/>
			<br />
			<CustomButton
				text="Check for Whitelisted Mods"
				secondary
				on:click={checkWhiteList}
			/>
		{/if}
		<div class="cards">
			{#if allInGame}
				{#if taState.ServerSettings.EnableTeams}
					<p>Teams</p>
					{#each teams as team}
						{#if team.Score}
							<Progress
								progressPercent={parseFloat(
									(team.Accuracy * 100).toFixed(2)
								)}
								text={team.Name}
							>
							</Progress>
							<p>
								<span style="float: left">
									{team.Score}
								</span>
							</p>
						{/if}
					{/each}
				{/if}
				<p>Players</p>
				{#each curMatch.Players.sort((a, b) => b.Score - a.Score) as player}
					<Progress
						progressPercent={parseFloat(
							(player.Accuracy * 100).toFixed(2)
						)}
						text={player.Name}
					>
						<!-- <div style="display: flex">
							<p>
								<b>{player.Name}</b>
							</p>
						</div> -->
					</Progress>
					<p>
						<span style="float: left">
							{player.Score}
						</span>
						<span style="float: right">
							{player.Combo}
						</span>
					</p>
					<br />
				{/each}
			{:else}
				{#each curMatch.Players.sort((a, b) => b.Score - a.Score) as player}
					<div
						in:receive={{ key: player.Id }}
						out:send={{ key: player.Id }}
						class="infoCard"
					>
						<img
							loading="lazy"
							class="playerImg mini"
							src="https://new.scoresaber.com/api/static/avatars/{player.UserId}.jpg"
							on:error={imgErr}
							alt="{player.Name}'s profile image'"
						/>
						<b>{player.Name}</b>
						<hr />
						<b>Score: </b>

						{player.Score}
						<br />
						<b>Accuracy: </b>
						{(player.Accuracy * 100).toFixed(2)}%
						<br />
						<b>Combo: </b>
						{player.Combo}
						<!-- <button on:click={() => updatePlayer(player)}>Update</button> -->
						<br />
						<hr />
						<b>Team: </b>
						{player.Team.Name}
						<br />
						<b>Play State: </b>
						{PlayStates[player.PlayState]}
						<br />
						<b>Download State: </b>
						{DownloadStates[player.DownloadState]}
						<hr />
						<CustomButton
							text="Kick"
							secondary
							on:click={() => kickFromMatch(player)}
						/>
					</div>
				{/each}
			{/if}
		</div>

		{#if previousScores.length > 0}
			<h3>Previous Scores</h3>
			<Accorian>
				{#each previousScores.reverse() as scoreSet}
					<AccordianTab>
						<span slot="title">{scoreSet[0].Beatmap.Name}</span>
						<ol slot="content">
							{#each scoreSet as result}
								<li>
									{result.User.Name}
									<br />
									<div style="margin-left:20px">
										Score:
										{result.User.Score}
										<br />
										Acc:
										{(result.User.Accuracy * 100).toFixed(
											2
										)}%
										<br />
										Result:
										{getTypeString(result.Type)}
									</div>
								</li>
							{/each}
						</ol>
					</AccordianTab>
				{/each}
			</Accorian>
		{/if}
	</div>
	<div>
		<h3>Controls</h3>
		<CustomInput bind:value={bsURL} placeHolder="Song URL/ID" />
		<CustomButton
			on:click={loadSong}
			primary
			disabled={playersDownloading || bsURL == "" || !bsURL}
			text={playersDownloading ? "Players Downloading" : "Load Song"}
		/>
		<br />
		{#if curMatch.SelectedLevel}
			<p><b>Loaded Map:</b> {curMatch.SelectedLevel.Name}</p>

			<Select
				bind:value={curMatch.SelectedCharacteristic}
				on:change={updateMatch}
			>
				{#each curMatch.SelectedLevel?.Characteristics as char}
					<option value={char}>{char.SerializedName}</option>
				{/each}
			</Select>
			{#if curMatch.SelectedCharacteristic}
				<Select
					bind:value={curMatch.SelectedDifficulty}
					on:change={updateMatch}
				>
					<option selected={!validDiff} disabled>
						Select a Difficulty
					</option>
					{#each curMatch.SelectedCharacteristic?.Difficulties.sort() as diff}
						<option
							selected={diff == curMatch.SelectedDifficulty}
							value={diff}>
							{BeatmapDifficulty[diff]}
						</option>
					{/each}
				</Select>
			{/if}

			{#if allWait || allInGame}
				<br />
				<div style="width: 100%; overflow: hidden;">
					<ul
						style="list-style:none; float:left; margin-right: 20px;"
					>
						{#each mapOptions as option}
							<li>
								<CustomCheckbox
									bind:checked={option.isSelected}
									label={option.name}
								/>
							</li>
						{/each}
					</ul>
					<ul style="list-style:none; float:left">
						{#each taSongOptions as option}
							<li>
								<CustomCheckbox
									bind:checked={option.isSelected}
									label={option.label}
								/>
							</li>
						{/each}
					</ul>
				</div>
				<br />
				{#if allWait}
					<CustomButton
						on:click={playSong}
						primary
						disabled={!canPlay || !bannedCheck}
						text="Play Song"
					/>
				{/if}
				{#if allInGame}
					<CustomButton
						on:click={returnToMenu}
						primary
						text="Return To Menu"
					/>
				{/if}
			{/if}
		{/if}
	</div>
</section>
<hr />
<br />
{#if !allInGame}
	<CustomButton
		on:click={() => dispatch("close")}
		secondary
		text="Close Match"
	/>
{/if}

